@import "../../util/variables";

@media (min-width: $xs-min-width) and (max-width: $xs-max-width) {
    .home-section {
        position: absolute;
        top: 35%;
        left: 5%;
        right: 10%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .home-section .home-name-title {
        text-align: center;
        color: white;
        font-family: "Fredoka One", cursive;
        font-size: 25px;
    }

    .home-section .home-description {
        text-align: center;
        color: white;
        font-family: "Roboto Slab", serif;
        font-size: 15px;
    }

    .home-section .home-social {
        display: flex;
        justify-content: space-around;
    }
}

